









export default {
	props: {
		/**
		 * 显示状态
		 */
		visible: {
			type: Boolean,
			default: false
		},
		/**
		 * 显示模式（左、右），只在初始化生效
		 */
		mode: String,
		/**
		 * 蒙层显示状态
		 */
		mask: {
			type: [Boolean, String],
			default: true
		}
	},
	data() {
		return {
			rightMode: false,
			catchtouchmove: false
		}
	},
	computed: {
		showMask() {
			return String(this.mask) === 'true'
		}
	},
	created() {
		this.rightMode = this.mode === 'right'

		this.catchtouchmove = true

	},
	methods: {
		close() {
			this.$emit('close')
		}
	}
}



